@import './components/globalStyles.scss';
.subscriberThanksBox {
    max-width: 400px;
    border-radius: 10px;
    font-size: $fontsizeSmallMedium;
    line-height: 1.3;
    padding: 20px 50px 20px 50px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

.subscriberThankButtonContainer {
    margin-top: 20px;
    text-align: center;
}

.subscriberThankButton {
    background-image: linear-gradient(to right, #3036FE, #009EFF);
    border: none;
    cursor: pointer;
    color: $colorWhite;
    font-size: $fontsizeSmallMedium;
    font-family: inherit;
    font-weight: 500;
    border-radius: 15px;
    padding: 5px 17px 5px 17px;
}

//============================================================================================
.subscribeBox {
    max-width: 400px;
    border-radius: 10px;
    padding: 5px 50px 20px 50px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.subscribeBoxTextInput {
    font-family: inherit;
    font-size: $fontsizeSmall;
    font-weight: 500;
    box-sizing: border-box;
    border-radius: 15px;
    border: none;
    padding: 6px 8px 6px 8px;
    margin-top: 20px;
    &:focus {
        border: none;
        text-decoration: none;
        outline: none;
    }
}

.subscribeButtonContainer {
    margin-top: 20px;
    text-align: center;
}

.subscribeButton {
    background-image: linear-gradient(to right, #3036FE, #009EFF);
    border: none;
    cursor: pointer;
    color: #EEEEEE;
    font-size: $fontsizeSmall;
    font-family: inherit;
    font-weight: 500;
    border-radius: 15px;
    padding: 5px 17px 5px 17px;
    &:focus {
        outline: none;
    }
}

.errorNotification {
    color: #E66767;
    font-size: $fontsizeSmallMedium;
    padding-top: 10px;
    font-weight: 500;
}

@media screen and (max-width: 600px) {
    .subscribeBox {
        padding: 5px 20px 20px 20px;
    }
}