@import "../globalStyles.scss";
//==========================================================================================
.icon {
    cursor: pointer;
    outline: none;
    &:focus {
        outline: none;
    }
}

.nav_icon_5 {
    width: 35px;
    height: 30px;
    position: relative;
    display: inline-block;
    cursor: pointer;
}

.nav_icon_5 span {
    background-color: $colorWhite;
    position: absolute;
    border-radius: 2px;
    transition: .3s cubic-bezier(.8, .5, .2, 1.4);
    width: 100%;
    height: 4px;
    transition-duration: 300ms;
}

.nav_icon_5 span:nth-child(1) {
    top: 0px;
    left: 0px;
}

.nav_icon_5 span:nth-child(2) {
    top: 13px;
    left: 0px;
    opacity: 1;
}

.nav_icon_5 span:nth-child(3) {
    bottom: 0px;
    left: 0px;
}

.nav_icon_5:not(.open):hover span:nth-child(1) {
    transform: rotate(-3deg) scaleY(1.1);
}

.nav_icon_5:not(.open):hover span:nth-child(2) {
    transform: rotate(3deg) scaleY(1.1);
}

.nav_icon_5:not(.open):hover span:nth-child(3) {
    transform: rotate(-4deg) scaleY(1.1);
}

.nav_icon_5.open span:nth-child(1) {
    transform: rotate(45deg);
    top: 13px;
}

.nav_icon_5.open span:nth-child(2) {
    opacity: 0;
}

.nav_icon_5.open span:nth-child(3) {
    transform: rotate(-45deg);
    top: 13px;
}

@media screen and (max-width: 600px) {
    .nav_icon_5 {
        width: 30px;
        height: 25.8px;
    }
    .nav_icon_5 span:nth-child(2) {
        top: 11.1px;
    }
}