@import '../globalStyles.scss';
.ideaBoxContainer {
    margin-top: 40px;
    margin-bottom: 30px;
    background-color: $colorBlacker;
    padding: 20px 50px 20px 50px;
    border-radius: 10px;
}

.ideaIcon {
    max-width: 100px;
}

.ideaBox {
    display: flex;
    flex-direction: column;
}

.ideaBoxTitle {
    font-size: $fontsizeSmallMedium;
    color: $colorWhite;
}

.textInput {
    font-family: inherit;
    font-size: $fontsizeSmall;
    font-weight: 500;
    color: $colorWhite;
    box-sizing: border-box;
    border-radius: 14px;
    -webkit-border-radius: 14px;
    -moz-border-radius: 14px;
    -ms-border-radius: 14px;
    -o-border-radius: 14px;
    border: 1px solid;
    background-color: $colorBlacker;
    padding: 5px 8px 5px 8px;
    margin-top: 15px;
    &::placeholder {
        font-size: $fontsizeSmall;
        font-weight: 500;
    }
    &:focus {
        //background-color: $colorBlack;
        text-decoration: none;
        outline: none;
    }
}

.ideaBoxBottomContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.emailField {
    flex-grow: 4;
}

.submitButtonContainer {
    flex-grow: 1;
    text-align: right;
}

.submitButton {
    background-image: linear-gradient(to right, #3036FE, #009EFF);
    border: none;
    cursor: pointer;
    color: #EEEEEE;
    font-size: $fontsizeSmall;
    font-family: inherit;
    font-weight: 500;
    border-radius: 15px;
    padding: 5px 17px 5px 17px;
    &:focus {
        outline: none;
    }
    margin-top: 15px;
    &:focus {
        outline: none;
    }
}

//===ERROR NOTIFICATION====================================================
.errorNotification {
    color: #E66767;
    font-size: $fontsizeSmallMedium;
    padding-top: 10px;
    font-weight: 500;
}

//==================================================================
.contactThanksBox {
    max-width: 400px;
    border-radius: 10px;
    font-size: $fontsizeSmallMedium;
    line-height: 1.3;
    padding: 20px 50px 20px 50px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.contactThankButtonContainer {
    margin-top: 20px;
    text-align: center;
}

.contactThankButton {
    background-image: linear-gradient(to right, #3036FE, #009EFF);
    border: none;
    cursor: pointer;
    color: $colorWhite;
    font-size: $fontsizeSmall;
    font-family: inherit;
    font-weight: 500;
    border-radius: 15px;
    padding: 5px 17px 5px 17px;
}

//==================================================================
@media screen and (max-width: 600px) {
    .ideaBoxContainer {
        padding: 10px;
    }
    .ideaBoxBottomContainer {
        flex-direction: column;
        align-items: normal;
    }
    .emailField {
        flex-grow: 1;
    }
    .submitButtonContainer {
        flex-grow: 1;
    }
}